/* public-sans-100normal - latin */
@font-face {
  font-family: 'Public Sans';
  font-style: normal;
  font-display: swap;
  font-weight: 100;
  src:
    local('Public Sans Thin '),
    local('Public Sans-Thin'),
    url('./files/public-sans-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/public-sans-latin-100.woff') format('woff'); /* Modern Browsers */
}

